<template>
  <el-container class="layout">
    <el-aside
      :style="{
        backgroundColor: themecolor,
        width: isCollapse ? '64px' : '200px'
      }"
    >
      <div class="title"><span>Bing</span></div>
      <el-menu
        :default-openeds="['1', '3']"
        :background-color="themecolor"
        active-text-color="#bacf65"
        text-color="#fff"
        :collapse="isCollapse"
        router
      >
        <el-submenu index="1">
          <template slot="title"
            ><i class="el-icon-s-custom"></i
            ><span slot="title">账号管理</span></template
          >
          <el-menu-item index="/rolelist"> 角色管理</el-menu-item>
          <el-menu-item index="1-2">账号管理</el-menu-item>
        </el-submenu>
        <el-menu-item index="/house">
          <i class="el-icon-office-building" /><span slot="title"
            >客房管理</span
          >
        </el-menu-item>

        <el-submenu index="3">
          <template slot="title"
            ><i class="el-icon-user"></i
            ><span slot="title">客户管理</span></template
          >
          <el-menu-item index="3-1"
            ><span slot="title">角色管理</span></el-menu-item
          >
          <el-menu-item index="3-2"
            ><span slot="title">账号管理</span></el-menu-item
          >
        </el-submenu>
        <el-submenu index="4">
          <template slot="title"
            ><i class="el-icon-setting"></i
            ><span slot="title">权限管理</span></template
          >
          <el-menu-item index="4-1"
            ><span slot="title">权限管理</span></el-menu-item
          >
          <el-menu-item index="4-2"
            ><span slot="title">账号管理</span></el-menu-item
          >
        </el-submenu>
      </el-menu>
    </el-aside>
    <el-container>
      <el-header :style="{ backgroundColor: themecolor }">
        <div @click="isCollapse = !isCollapse" style="font-size: 25px">
          <i :class="isCollapse ? 'el-icon-s-unfold' : 'el-icon-s-fold'" />
        </div>
        <el-menu
          :default-active="index1"
          class="el-menu-demo"
          mode="horizontal"
          @select="handleSelect"
          :background-color="themecolor"
          active-text-color="#fbb929"
          text-color="#fff"
          router
        >
          <el-menu-item index="1">
            <i class="el-icon-s-home" />主页</el-menu-item
          >
          <el-submenu index="2">
            <template slot="title"><i class="el-icon-setting" />主题</template>
            <el-menu-item
              @click="themeChange(item.value)"
              v-for="(item, index) in theme"
              :key="index"
            >
              {{ item.name }}
            </el-menu-item>
          </el-submenu>
          <el-menu-item index="/message">
            <i class="el-icon-message" /> 邮箱
          </el-menu-item>
          <el-menu-item index="2">
            <i class="el-icon-s-comment" /> 消息
          </el-menu-item>
          <el-submenu index="4">
            <template slot="title"
              ><i class="el-icon-user-solid" />管理员</template
            >
            <el-menu-item index="4-1"> 修改密码 </el-menu-item
            ><el-menu-item @click="exit()">退出 </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-header>
      <el-main>
        <picture1 :d="res_list" @tl="tl" class="do">
          <div id="verify">
            <div id="handle">
              <span></span>
            </div>
          </div>
        </picture1>
        <!-- <router-view></router-view> -->
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
import { mapState } from "vuex";
import { mapMutations } from "vuex";
import picture1 from "../house/index";

export default {
  components: { picture1 },
  // async created() {
  //   let username = localStorage.getItem("username");
  //   if (username) {
  //     let results = await this.axios.get("/api/list", {
  //       params: {
  //         id: username
  //       }
  //     });
  //     let user_info = results.data[0];
  //     this.setusers({ username, user_info });
  //     this.res_list.push(user_info);
  //   } else {
  //     this.$router.push("/");
  //   }
  // },
  mounted() {
    let shouldMove = false;
    const captcha = document.querySelector("#verify");
    const handle = document.querySelector("#handle");
    const button = document.querySelector("#handle span");
    console.log(handle);
    button.addEventListener("mousedown", () => {
      shouldMove = true;
    });

    window.addEventListener("mousemove", e => {
      if (shouldMove) {
        const offsetLeft = handle.getBoundingClientRect().left;
        const buttonWidth = button.getBoundingClientRect().width;

        captcha.style.setProperty(
          "--moved",
          `${e.clientX - offsetLeft - buttonWidth / 2}px`
        );
      }
    });

    window.addEventListener("mouseup", e => {
      if (shouldMove) {
        const finalOffset = e.clientX - handle.getBoundingClientRect().left;

        if (finalOffset >= 330 && finalOffset <= 350) {
          // pass
          captcha.classList.add("passed");
        } else {
          // failed
          captcha.style.setProperty("--moved", "0px");
        }

        shouldMove = false;
      }
    });
  },
  computed: {
    ...mapState("theme", ["theme"])
  },
  data() {
    return {
      res_list: [],
      index1: "1",
      isCollapse: false,
      themecolor: localStorage.getItem("themecolor") || "#ede3e7"
    };
  },
  methods: {
    tl(e) {
      console.log("子组件", e);
    },
    ...mapMutations("users", ["setusers"]),
    exit() {
      this.$router.push("/");
      sessionStorage.clear();
    },
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    },
    themeChange(color) {
      this.themecolor = color;
      localStorage.setItem("themecolor", this.themecolor);
    }
  }
};
</script>

<style lang="scss" scoped>
.do {
  text-align: center;
  font-size: 44px;
}
.layout {
  width: 100%;
  height: 100vh;
  .el-header {
    min-width: 650px;
    padding: auto 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.el-aside {
  overflow: hidden;
  .title {
    line-height: 38px;
    font-family: _GB2312 KaiTi_GB2312;
    margin: 10px 10px;
    color: #fff;
    text-align: center;
    border: 1px solid white;
    border-radius: 4px;
  }
  .el-menu:not(.el-menu--collapse) {
    width: 200px;
    min-height: 400px;
    .el-submenu {
      width: 200px;
    }
  }
}
.el-main {
  --moved: 0px;
  --width: 300px;
  #verify {
    width: 300px;
    height: 200px;
    background-size: cover;
    display: block;
    background-image: url("../../../public/img/e261b1a44ca68a9e5be68aeafe7fe83e.jpg");
    position: relative;
    left: 150px;
    #handle {
      width: 460px;
      height: 30px;
      border-radius: 18px;
      position: absolute;
      bottom: -50px;
      background-color: #eee;
      left: -160px;
      box-shadow: inset 0px 0px 12px rgba(0, 0, 0, 0.3);
      border: 3px solid #ccc;
      span {
        display: block;
        width: 80px;
        height: inherit;
        background-color: #fff;
        box-shadow: inset 0px 0px 6px rgba(0, 0, 0, 0.25),
          0px 2px 4px rgba(0, 0, 0, 0.3);
        position: absolute;
        border-radius: 12px;
        cursor: move;
        transform: translatex(clamp(0px, var(--moved), 380px));
      }
    }
  }
  #verify::before,
  #verify::after {
    content: "";
    display: block;
    width: inherit;
    height: inherit;
    background-size: inherit;
    display: block;
    background-image: inherit;
    position: absolute;
    -webkit-clip-path: inset(60px 80px 60px 140px);
    clip-path: inset(60px 80px 60px 140px);
  }
  #verify::before {
    background-color: rgba(0, 0, 0, 0.6);
    background-blend-mode: multiply;
  }
  #verify::after {
    transform: translatex(
      clamp(-300px, calc(var(--moved) + var(--width) * -1), 80px)
    );
  }
  #verify.passed::before,
  #verify.passed::after,
  #verify.passed #handle {
    opacity: 0;
  }
}
</style>
